ul {
    list-style-type: none;
}

* {
    border: 0;
    margin: 0;
    padding: 0;
}

span {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(247, 247, 247);
}

h1 {
    font-family: 'Berkshire Swash', cursive;
    color: #412A22;
    font-size: 3rem;
}

body {
    background: #c7b38f;
}
.homepage-image-container {
  height: 100vh;
  width: 100%;
  overflow: hidden; }

.homepage-image {
  object-fit: cover;
  width: 100%;
  height: 100%; }

.book {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 40%;
  max-width: 150px;
  height: 8vh;
  background-color: #825534;
  border: 2px dotted white;
  border-radius: 20px;
  color: white;
  font-size: 1.5rem;
  font-family: 'Lato', sans-serif;
  -webkit-transition: color 0.5s, background-color 0.5s, border 0.5s;
  transition: color 0.5s, background-color 0.5s, border 0.5s;
  z-index: 99; }

.book:hover {
  background-color: white;
  color: #825534;
  border: 4px solid #825534; }

.logo {
  background: transparent;
  border: #c7b38f 3px solid;
  background-color: transparent;
  position: absolute;
  top: 30%;
  width: 60%;
  max-width: 250px;
  left: 0;
  right: 0;
  margin: 0 auto;
  text-align: center;
  z-index: 99; }

.logo-text {
  font-style: italic;
  font-family: 'Sevillana', cursive;
  color: white;
  font-size: 2.5rem; }

.menu-line {
  background-color: #412a12;
  height: 10px; }

.menu-icon {
  position: fixed;
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 2vh;
  right: 3vw;
  z-index: 99; }

.menu-icon:focus {
  display: none; }

.menu-list {
  display: none; }

.menu-icon:focus + .menu-list {
  position: fixed;
  display: flex;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 2rem;
  height: 20vh;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  text-align: center; }
  .menu-icon:focus + .menu-list li {
    width: 30vw;
    background-color: transparent;
    color: white; }

@media only screen and (min-width: 1600px) and (orientation: landscape) {
  .homepage-image {
    -webkit-animation: scroll 30s linear 0s infinite both;
            animation: scroll 30s linear 0s infinite both;
    height: auto; }
  @-webkit-keyframes scroll {
    0%, 5% {
      -webkit-transform: translateY(0);
              transform: translateY(0); }
    50%, 55% {
      -webkit-transform: translateY(-15%);
              transform: translateY(-15%); }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0); } }
  @keyframes scroll {
    0%, 5% {
      -webkit-transform: translateY(0);
              transform: translateY(0); }
    50%, 55% {
      -webkit-transform: translateY(-15%);
              transform: translateY(-15%); }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0); } } }

.food-gallery {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr; }

.food {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 60vh; }

.food-image {
  width: 80%;
  height: auto; }

.food-name {
  font-size: 3rem;
  font-family: 'Montez', cursive; }

.food-price {
  font-size: 2rem;
  font-family: 'Ewert', cursive;
  color: black; }

@media only screen and (min-width: 480px) {
  .food-gallery {
    grid-template-columns: auto auto;
    justify-content: space-evenly; }
  .food {
    max-width: 200px; }
  .food-image {
    width: 100%; } }

@media only screen and (min-width: 768px) {
  .food {
    max-width: 300px; } }

@media only screen and (min-width: 1024px) {
  .food {
    max-width: 400px; } }

@media only screen and (min-width: 1440px) {
  .food-gallery {
    grid-template-columns: auto auto auto; }
  .food {
    max-width: 400px; } }

@media only screen and (min-width: 2560px) {
  .food {
    max-width: 600px; } }

.wine-gallery {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: space-between; }

.wine {
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly; }

.wine-image {
  width: 60vw;
  height: auto; }

.wine-name {
  font-size: 3rem;
  font-family: 'Montez', cursive; }

.wine-price {
  font-size: 2rem;
  font-family: 'Ewert', cursive;
  color: black; }

.contact-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly; }

.book-form {
  background-color: #825534;
  width: 90%;
  max-width: 500px;
  margin: 5vh auto 0;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 5%;
  align-items: center; }

.input-container {
  width: 55.55556%;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.form-input {
  display: block;
  width: 100%;
  height: 6vh;
  border: black solid 2px;
  background-color: #e2c39b;
  border-radius: 10px;
  font-size: 1.2rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding-left: 5%; }

.book-title {
  color: white;
  font-size: 2rem;
  border-radius: 50%;
  border: white dashed 3px;
  width: 66.66667%;
  height: 10vh;
  text-align: center;
  line-height: 10vh;
  font-family: 'Sevillana', cursive;
  text-transform: capitalize; }

.form-label {
  color: white;
  font-size: 2rem;
  font-family: 'Great Vibes', cursive;
  text-align: center; }

.book-submit {
  background-color: #a7a37e;
  width: 33.33333%;
  height: 6vh;
  border-radius: 10px;
  border: white solid 2px;
  font-size: 1.25rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: saddlebrown;
  -webkit-transition: color 0.5s, background-color 0.5s, border 0.5s;
  transition: color 0.5s, background-color 0.5s, border 0.5s; }

.book-submit:hover {
  background-color: white;
  color: #825534;
  border: 4px solid #a7a37e; }

.address-container {
  margin: 5vh 0 0; }

.address-heading {
  font-family: 'Lato', sans-serif;
  color: #412A22;
  font-size: 1.5rem;
  display: inline-block; }

.address {
  font-family: 'Lato', sans-serif;
  font-size: 1.5rem; }

.bingmaps {
  margin: 5vh 0 0;
  width: 100%;
  height: 40vh; }

.descr-list {
  width: 100%; }

.description {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }

.descr-text {
  width: 90vw;
  font-size: 1.5rem; }

.descr-image {
  width: 90vw;
  height: auto;
  margin: 5vh 0; }

